body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.4;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  -webkit-user-select: none;
}
.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  flex-wrap: wrap;
}
.header {
  display: block;
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}
.header__top .header__top-logo {
  display: flex;
  align-items: center;
}
.header__top .header__top-logo .name-site {
  color: #000;
  font-size: 30px;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-right: 10px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.header__top .header__top-logo .name-site:hover {
  color: #1676d8;
  box-shadow: 0 0 10px 5px #1676d8;
  transition: 0.8s;
}
.header__top .header__top-logo .top__logo-subtext {
  max-width: 290px;
  border-left: 1px solid #1676d8;
}
.header__top .header__top-logo .top__logo-subtext p {
  font-size: 14px;
  line-height: 14px;
  margin-left: 10px;
  padding: 8px 0px;
}
.header__top .header__top-email {
  display: flex;
  flex-direction: column;
}
.header__top .header__top-email .info {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.header__top .header__top-email .info img {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}
.header__top .header__top-email .info a {
  font-size: 14px;
  color: #9b373a;
}
.header__top .header__top-email .info .telegram {
  font-size: 15px;
  color: #9b373a;
}
.header__top .header__top-email .info .telegram:hover {
  font-size: 16px;
  cursor: pointer;
  margin-left: -1px;
}
.header__top .header__top-email .info a:hover {
  color: #9b373a;
  cursor: pointer;
  text-decoration: underline;
}
.header__top .header__top-btn {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
.header__top .header__top-btn .top__btn-b {
  background: #1676d8;
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  text-align: center;
  width: 262px;
  padding: 10px 0;
  margin-bottom: 10px;
}
.header__top .header__top-btn .top__btn-g {
  background: #73bc13;
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  text-align: center;
  width: 262px;
  padding: 10px 0;
  margin-bottom: 20px;
}
.header__top .header__top-btn .btn:hover {
  cursor: pointer;
  opacity: 0.9;
}
.header__nav {
  border-bottom: 2px solid #1676d8;
  background: #f5f5f5;
}
.header .menu .menu,
.header .menu li {
  position: relative;
}
.header .menu .menu__list {
  display: flex;
  justify-content: space-between;
}
.header .menu .menu__list li {
  padding: 11px 15px;
  width: 100%;
  text-align: center;
}
.header .menu .menu__list li .menu__link {
  color: #000;
  font-size: 14px;
  text-transform: uppercase;
}
.header .menu .menu__list li .sub-menu__list {
  display: none;
  position: absolute;
  left: 0;
}
.header .menu .menu__list li .sub-menu__list li {
  min-width: 174px;
  margin-top: 15px;
}
.header .menu .menu__list li .sub-menu__list li .sub-menu__link {
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  display: inline-block;
}
.header .menu .menu__list li .sub-menu__list li .sub-menu__link:hover {
  text-decoration: underline;
}
.header .menu .menu__list li .sub-menu__list > li {
  background: #1676d8;
}
.header .menu .menu__list li:hover .sub-menu__list {
  display: block;
}
.header .menu .menu__list li:hover {
  background: #1676d8;
  text-decoration: underline;
  cursor: pointer;
}
.header .menu .menu__list li:hover > a {
  color: #fff;
}
