.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.795);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-x: hidden;
  overflow-y: auto;
  transition: all 0.5s ease 0s;
  z-index: 999;
}
.popup .popup__body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
}
.popup .popup__body .popup__content {
  max-width: 700px;
  padding: 30px;
  position: relative;
  opacity: 0;
  transform: translate(0px, -100%);
}
.popup .popup__body .popup__content .popup__close {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
}
.popup .popup__body .popup__content .popup__form .form-popup {
  display: flex;
  flex-direction: column;
}
.popup .popup__body .popup__content .popup__form .form-popup input {
  border: none;
  background: #f5f5f5;
  padding: 12px 24px;
  font-size: 18px;
  width: 380px;
  height: 27px;
  margin-bottom: 20px;
  border-radius: 5px;
}
